    <div class="form-content">
        <div class="from-item">
            <div class="f-label"  @click="showCountry = true" >
                <img class="flag" :src="country.flag" alt="">
                +{{country.calling_code}}
                <van-icon  class="icon-more" name="play" />
            </div>
            <div class="f-value">
                <input class="f-input"  v-model="form.username"  placeholder="电话" type="tel"  > 
