            </div>
        </div>   
             <div style="margin: 30px 0;">
                <van-button :loading="loading" round block color="#C1272D" @click="onSubmit">
                     提交
                </van-button>
                <button class="text-btn">忘记密码？</button>
            </div>           
    </div> 
</div>
</template>

<script>
import {login,getCountryCode} from './service'
import Vue from 'vue';
import { Popup,Icon,Form,Field,Button,Toast,IndexAnchor,IndexBar,Cell } from 'vant';
Vue.use(IndexBar);
Vue.use(Cell);
Vue.use(IndexAnchor);
Vue.use(Toast);
Vue.use(Button);
Vue.use(Icon);
Vue.use(Popup);
Vue.use(Form);
Vue.use(Field);
export default {
components: {},
data() {
    return {
        showCountry:false,
        country:{},
        loading:false,
        showPopup:false,
        form:{
            username:'',
            password:'',
        },
        countryList:[
            {index:'A',list:[]},
            {index:'B',list:[]},
            {index:'C',list:[]},
            {index:'D',list:[]},
            {index:'E',list:[]},
            {index:'F',list:[]},
            {index:'G',list:[]},
            {index:'H',list:[]},
            {index:'I',list:[]},
            {index:'J',list:[]},
            {index:'K',list:[]},
            {index:'L',list:[]},
            {index:'M',list:[]},
            {index:'N',list:[]},
            {index:'O',list:[]},
            {index:'P',list:[]},
            {index:'Q',list:[]},
            {index:'S',list:[]},
            {index:'T',list:[]},
            {index:'U',list:[]},
            {index:'V',list:[]},
            {index:'W',list:[]},
            {index:'X',list:[]},
            {index:'Y',list:[]},
            {index:'Z',list:[]},
        ]
    };
},
computed: {},
watch: {},
methods: {
    selCountry(row){
        this.country = row;
        this.showCountry = false;
    },
    getCountryCode(){
        getCountryCode().then(res=>{
              let vm = this;
              res.data.forEach(item=>{
                  if(item.iso3==res.meta.default.iso3){
                      vm.country = item;
                  }
              }) 
              this.countryList.forEach(item=>{
                res.data.forEach(subItem=>{
                    if(item.index==subItem.label.substr(0, 1)){
                        item.list.push(subItem);
                    }
                })                  
              })  
        }).catch(res=>{
            Toast(res.message);
        })
    },
    toRegister(){
         this.$router.push({path:'/register'});
    },
    onSubmit(){
        this.loading = true;
        let obj = Object.assign({},this.form);
        obj.username = '+'+this.country.calling_code+'-'+obj.username;
        login(obj).then(res=>{
             Toast.success('登录成功');
             let data  = JSON.stringify(res);
             window.localStorage.setItem('user',data); 
             this.loading = false;
        }).catch(res=>{
            Toast(res.message);
            this.loading = false;
        })
    }
},
created() {

},
mounted() {
    this.getCountryCode();
},
beforeCreate() {},
beforeMount() {},
beforeUpdate() {},
updated() {},
beforeDestroy() {},
destroyed() {},
activated() {},
}
</script>
